.svg-background-1 {
  $widthD: 68px;
  $widthB: 55px;
  $widthC: 66px;
  $circlePink: (4800%/470);
  $circleGreen: (6200%/470);
  $circleYellow: (3700%/470);
  padding: 18px 19px 28px 23px;
  @include media-breakpoint-up('sm') {
    padding: 0 15px;
  }

  .symbol, .circle {
    position: absolute;
    height: auto;
  }
  .h1 {
    margin-right: 30%;
    max-width: 175px;
    @include media-breakpoint-up('sm') {
      margin-right: 0;
      max-width: none;
    }
  }
  .symbol-d {
    width: $widthD;
    right: 14px;
    top: 0;
    margin-top: -$widthD/2;
    @include media-breakpoint-up('sm') {
      top: -74%;
      left: 47%;
      right: auto;
      bottom: auto;
      margin-top: 0;
    }
  }
  .symbol-c {
    width: $widthC;
    right: -10px;
    top: 0;
    margin-top: $widthD/2;
    @include media-breakpoint-up('sm') {
      top: 95%;
      left: 70%;
      bottom: auto;
      right: auto;
      margin-top: 0;
    }
  }
  .symbol-b {
    width: $widthB;
    right: -10px;
    top: 0;
    margin-right: 0.8 * $widthC;
    margin-top: 0.6 * $widthD/2;
    z-index: -1;
    @include media-breakpoint-up('sm') {
      left: -23%;
      top: 25%;
      right: auto;
      margin-top: 0;
      margin-right: 0;
    }
  }
  .circle-green {
    left: 0;
    width: $circleGreen;
    top: 0;
    margin-top: -$circleGreen*0.6;
    @include media-breakpoint-up('sm') {
      top: -84%;
      margin-top: 0;
    }
  }
  .circle-pink {
    bottom: 0px;
    left: 5px;
    width: $circlePink;
    @include media-breakpoint-up('sm') {
      top: 115%;
      left: 45%;
      bottom: auto;
      right: auto;
    }
  }
  .circle-yellow {
    right: 0; 
    top: 100%;
    width: $circleYellow;
    @include media-breakpoint-up('sm') {
      left: 97% - $circleYellow;
      top:-50%;
      bottom: auto;
      right: auto;
    }
  }
  .shadow {
    position: absolute;
    z-index: -2;
    width: 110%;
    left: 6%;
    top: 90%;
    @include media-breakpoint-up('sm') {
      top: 107%;
      left: 3%;
      bottom: auto;
    }
  }
  svg.background  {
    width: 100%;
    height: 100%;
    display: none;
    @include media-breakpoint-up('sm') {
      height: 169%;
      width: 116.4%;
      display: block;
    }
    &.mob {
      display: block;
      @include media-breakpoint-up('sm') {
        display: none;
      }
    }
  }
  @include media-breakpoint-up('sm') {
    $widthD: (11100%/470);
    $widthB: (9000%/470);
    $widthC: (10800%/470);
    .symbol-d {
      width: $widthD;
      margin-top: -$widthD/2;
    }
    .symbol-c {
      width: $widthC;
      margin-top: $widthD/2;
    }
    .symbol-b {
      width: $widthB;
      margin-right: 0.8 * $widthC;
      margin-top: 0.6 * $widthD/2;
    }
  }
  
}

.svg-background-2 {
  padding: 18px 7px 22px 24px;
  @include media-breakpoint-up('md') {
    padding: 0;
  }
  @include media-breakpoint-down('sm') {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
  svg.background {
    width: 100%;
    height: 100%;
    display: none;
    @include media-breakpoint-up('md') {
      width: (63600%/526);
      height: (32600%/246);
      display: block;
    }
    &.mob {
      display: block;
      @include media-breakpoint-up('md') {
        display: none;
      }
    }
  }
  .shadow {
    position: absolute;
    z-index: -2;
    width: (66800%/526);
    left: 30px;
    max-width: none;
    top: 89%;
    @include media-breakpoint-up('md') {
      top: 92%;
      left: (-3000%/526);
    }
  }
}

.svg-background--list {
  svg.background {
    height: 180.5%;
  }
  &-1 {
    svg.background {
      width: (21000%/180);
      @include media-breakpoint-up('md') {
        width: (33600%/248);
      }
    }
  }
  &-2 {
    svg.background {
      width: (19000%/167);
      @include media-breakpoint-up('md') {
        width: (31900%/235);
      }
    }
  }
  &-3 {
    svg.background {
      width: (24600%/218);
      @include media-breakpoint-up('md') {
        width: (39600%/307);
      }
    }
  }
  &-4 {
    svg.background {
      width: (19500%/177);
      @include media-breakpoint-up('md') {
        width: (40600%/331);
      }
    }
  }
  &-5 {
    svg.background {
      width: (18800%/162);
      @include media-breakpoint-up('md') {
        width: (30200%/223);
      }
    }
  }
  &-6 {
    svg.background {
      width: (19100%/164);
      @include media-breakpoint-up('md') {
        width: (31000%/225);
      }
    }
  }
}

.svg-background-3 {
  @include media-breakpoint-down('xs') {
    padding: 11px 4px 14px;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
  & > svg.background {
    display: none;
    width: calc(100% + 20px);
    height: 100%;
    @include media-breakpoint-up('sm') {
      display: block;
      width: (56100%/544);
      height: 115%;
      left: 46%;
    }
    &.mob {
      display: block;
      @include media-breakpoint-up('sm') {
        display: none;
      }
    }
  }
}

.svg-background-4 {
  max-width: 490px;
  @include media-breakpoint-down('xs') {
    padding: 20px 4px 19px;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
  & > svg.background {
    display: none;
    width: calc(100% + 20px);
    height: 100%;
    @include media-breakpoint-up('sm') {
      display: block;
      $width: (51400%/490);
      width: $width;
      left: 100% - $width/2; 
      height: (14800%/120);
    }
    &.mob {
      display: block;
      @include media-breakpoint-up('sm') {
        display: none;
      }
    }
  }
}

.svg-background-5 {
  max-width: 480px;
  @include media-breakpoint-down('xs') {
    padding: 12px 4px 15px;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
  & > svg.background {
    display: none;
    width: calc(100% + 20px);
    height: 100%;
    @include media-breakpoint-up('sm') {
      display: block;
      $width: (51000%/480);
      width: $width;
      height: (14000%/110);
    }
    &.mob {
      display: block;
      @include media-breakpoint-up('sm') {
        display: none;
      }
    }
  }
}

.svg-background-6 {
  padding-left: 20px;
  padding-right: 21px;
  padding-top: 22px;
  padding-bottom: 34px;
  @include media-breakpoint-up('sm') {
    padding-left: (5800%/555);
    padding-right: (10000%/555);
    padding-top: 0;
    padding-bottom: 0;
  }
  & > svg.background {
    width: 100%;
    height: 100%;
    display: none;
    @include media-breakpoint-up('sm') {
      display: block;
      height: 131.6%;
    }
    &.mob {
      display: block;
      @include media-breakpoint-up('sm') {
        display: none;   
      }
    }
  }
  .shadow {
    position: absolute;
    z-index: -1;
    width: 92.03%;
    right: 2.4%;
    height: auto;
    bottom: -0;
    margin-bottom: -50px;
    @include media-breakpoint-up('sm') {
      margin-bottom: -19.5%;
    }
    @include media-breakpoint-up('lg') {
    }
    @include media-breakpoint-up('xl') {
    }
  }
}