
.big-ul-list {
  list-style: none;
  margin-bottom: 0;
  color: $text-color;
  counter-reset:num;
  li {
    position: relative;
    display: flex;
    align-items: center;
    @include media-breakpoint-down('xs') {
      font-size: 1rem;
    }
    &:before {
      content: counter(num); 
      counter-increment: num;
      display: inline-block;	
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);   
      color: rgba(255, 255, 255, 0.5);
      text-align: right;
      line-height: 1;
      font-weight: 700;
    }
  }
  li + li {
    // margin-top: 24px;
  }
}

.big-ul-list {
  $font-size-li-num: 80px;

  padding-left: $font-size-li-num * 0.75;
  li {
    min-height: $font-size-li-num - 20;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  li:before {
    font-size: $font-size-li-num;
    left: -$font-size-li-num * 0.75;
  }

  @include media-breakpoint-up('md') {
    padding-left: $font-size-li-num * 1.1;
    li {
      min-height: $font-size-li-num - 20;
      margin-top: 10px;
      margin-bottom: 10px;
    }
    li:before {
      font-size: $font-size-li-num;
      left: -$font-size-li-num * 1.1;
    }
  }

  @include media-breakpoint-up('lg') {
    $font-size-li-num: 110px;

    padding-left: $font-size-li-num * (120/110);
    li {
      min-height: $font-size-li-num - 20;
      margin-top: 10px;
      margin-bottom: 10px;
    }
    li:before {
      font-size: $font-size-li-num;
      left: -$font-size-li-num;
    }
  }
}

