input.radio {
  position: absolute;
  width: 0;
  bottom: 0;
  pointer-events: none;
}

input.radio + label {
  display: flex;
  align-items: center;
  position: relative;
  line-height: 1;
  margin-bottom: 16px;
}

input.radio + label:before {
  content: '';
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 1px solid currentColor;
  margin-right: 8px;
  transition: border-color 0.2s;
  flex: 0 0 auto;
  align-self: flex-start;
  margin-top: 2px;
}

input.radio + label:after {
  content: '';
  position: absolute;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  min-width: 6px;
  min-height: 6px;
  background: currentColor;
  left: 8px;
  top: 10px;
  transform: translate(-50%, -50%) scale(0);
  transition: transform 0.2s, background-color 0.2s;
}

input.radio:checked + label:after {
  transform: translate(-50%, -50%) scale(1);
}

input.radio.input-invalid + label:before {
  border-color: #800000;
}

input.radio.input-invalid + label:after {
  background: #800000;
}

