.footer {
	width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  @include media-breakpoint-up('md') {
    padding-top: 36px;
    padding-bottom: 22px;
  }
  hr {
    border: 0;
    border-bottom: 1px solid #0098B8;
    margin-top: 18px;
    margin-bottom: 18px;
		@include media-breakpoint-up('lg') {
      margin-top: 28px;
    }
  }
	&__title-warning {
		text-transform: uppercase;
		font-weight: 300;
		text-align: center;
    font-size: 32px;
		@include media-breakpoint-up('md') {
      br {
        display: none;
      }
  		white-space: nowrap;
		}
		@include media-breakpoint-up('lg') {
			font-size: 43px;
		}		
    @include media-breakpoint-up('xl') {
			font-size: 48px;
		}
		@include media-breakpoint-up('xxl') {
			font-size: 53px;
		}
	}
	.row {
		flex-direction: column;
    margin-top: (-30em/16);
		@include media-breakpoint-up('sm') {
			flex-direction: row;
		}
		@include media-breakpoint-up('lg') {
		  flex-wrap: nowrap;
      margin-top: (-20em/16);
		}
		@include media-breakpoint-up('xl') {
			// margin-right: -25px;
			// margin-left: -25px;
		}
		.col {
		  width: auto;
		  margin-top: (30em/16);
      text-align: center;
      p {
        margin-left: auto;
        margin-right: auto;
      }
      @include media-breakpoint-up('sm') {
        text-align: left;
        p {
          margin-left: 0;
          margin-right: 0;
        }
      }
		  @include media-breakpoint-up('xl') {
		  	// padding-right: 25px;
		  	// padding-left: 25px;
		  }
	  }
	}


	&__adress {
		@include media-breakpoint-up('sm') {
		  flex: 7 1 0;
		}
	}

	&__contact {
		@include media-breakpoint-up('sm') {
		  flex: 6 1 0;
		}
	}

	&__links {
		flex: 0 1 auto;
    .social-links__wrapper {
      justify-content: center;
    }
		@include media-breakpoint-up('sm') {
      .social-links__wrapper {
        justify-content: flex-end;
      }
    }
		@include media-breakpoint-up('md') {
		  text-align: right !important;
		}
	}
}