.h1 {
  font-family: $font-accent;
  font-weight: 400;
  line-height: (44/36);
  font-size: (24rem/16);
  @include media-breakpoint-up('sm') {
    font-size: (28rem/16);
  }
  @include media-breakpoint-up('lg') {
    font-size: (32rem/16);
  }
  @include media-breakpoint-up('xl') {
    font-size: (36rem/16);
  }
}
.h2 {
  font-weight: 700;
  font-size: (18rem/16);
  line-height: (24/22);
  @include media-breakpoint-up('sm') {
    font-size: (20rem/16);
  }
  @include media-breakpoint-up('xl') {
    font-size: (22rem/16);
  }
}