
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,700;1,400;1,700&display=swap);
@font-face {
  font-family: 'Prosto One';
  src: local('Prosto One'), local('ProstoOne-Regular'),
      url('../fonts/ProstoOne-Regular.woff2') format('woff2'),
      url('../fonts/ProstoOne-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
html {
  font-size: 1rem;
}
body {
  color: $text-color;
  font-family: $font-main;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
}
.big-text {
  
}


a {
  text-decoration: none;
  color: $text-color;
  &:hover {
    color: $text-color;
  }
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 0;
  }
}

.color-text {
  color: $text-color !important;
}