.header {
  position: relative;
  padding-right: 15px;
  padding-left: 15px;
  margin-bottom: -15px;
  @include media-breakpoint-up('sm') {
    padding-right: 30px;
    padding-left: 30px;
  }
  .container {
    position: relative;
    z-index: 0;
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 20px;
    z-index: 1000;
    & > svg.background {
      position: absolute;
      top: 0%;
      left: 50%;
      transform: translate(-50%, -0%);
      width: 122.5%;
      height: 202.5%;
      z-index: -1;
      pointer-events: none;
    }
  }
  &__logo {
    border-right: 2px solid rgba($accent-color, 0.2);
    margin-right: 20px;
    flex: 0 1 auto;
    width: 88px;
    padding-right: 22px;
    @include media-breakpoint-up('sm') {
      width: 138px;
    }
  }
  &__nav {
    white-space: nowrap;
    align-self: center;
    flex: 1 2 auto;
    display: none;
    @include media-breakpoint-up('md') {
      display: block;
    }
    &__wrapper {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: 100%;
      margin-left: auto;
      margin-right: auto;
      max-width: 440px;
      @include media-breakpoint-up('xl') {
        max-width: none;
        margin-left: 0;
        margin-right: 0;
      }
    }
    a {
      margin: 0px 2%;
      margin: 0px \max(2%, 8px);
      display: inline-flex;
      align-items: center;
      min-height: (30em/16);
    }
  }
  .social-links {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 1 1 auto;
    padding-left: 15px;
    @include media-breakpoint-up('md') {
      border-left: 2px solid rgba($accent-color, 0.2);
      max-width: 220px;
      margin-left: 20px;
    }
    &__wrapper {
      flex-wrap: nowrap;
    }
  }
}