.mouse-move-box {
  position: relative;

  &__wrapper {
    position: relative;
  }
  img {
    position: absolute;
    max-width: none;
  }
  .mouse-move-box__layout-1 {
    z-index: 1;
  }
  .mouse-move-box__layout-2 {
    z-index: 2;
  }
  .mouse-move-box__layout-3 {
    z-index: 3;
  }
  .mouse-move-box__layout-4 {
    z-index: 4;
  }
}

.courser-browser .mouse-move-box {
  &__wrapper {
    will-change: transform;
    transition: transform 0.1s;
    transform-style: preserve-3d;
  }
  .mouse-move-box__layout-1 {
    transform: translateZ(0);
  }
  .mouse-move-box__layout-2 {
    transform: translateZ(20px);
  }
  .mouse-move-box__layout-3 {
    transform: translateZ(40px);
  }
  .mouse-move-box__layout-4 {
    transform: translateZ(80px);
  }
}

.mouse-move-box-1 {
  margin-bottom: 20px;
  @include media-breakpoint-up('sm') {
    margin-bottom: 0;
  }
  .mouse-move-box__wrapper:before {
    content: '';
    display: block;
    width: 0;
    padding-top: (78000%/555);
    @include media-breakpoint-up('sm') {
      padding-top: (62000%/555);
    }
  } 

  &__back {
    max-width: 130vw !important;   
    height: auto;
    bottom: 5%;
    left: 50%;
    @include media-breakpoint-down('xs') {
      transform: translate(-50%, 0) !important;
    }
    @include media-breakpoint-up('sm') {
      $widthBack: (54000%/555);
      top: 3%;
      bottom: auto;
      left: (100% - $widthBack)/2;
      width: $widthBack;
      max-width:none !important;
    }
  }

  &__main {
    $widthMain: (74400%/555);
    width: $widthMain;
    height: auto;
    top: 20%;
    left: (100% - $widthMain)/2;
    @include media-breakpoint-up('sm') {
      top: 0;
    }
  }

  &__cloud-1 {
    $widthCloud1: (16000%/555);
    width: $widthCloud1;
    height: auto;
    right: $widthCloud1 * -0.2;
    top: 1.7%;
    display: none;
    @include media-breakpoint-up('sm') {
      display: block;
    }
  }

  &__cloud-2 {
    $widthCloud2: (14900%/555);
    width: $widthCloud2;
    height: auto;
    top: 27.5%;
    left: $widthCloud2 * -0.35;
    display: none;
    @include media-breakpoint-up('sm') {
      display: block;
    }
  }
}

