.section-1 {
  position: relative;
  z-index: 0;
  min-height: calc(100vh - 100px);
  display: flex;
  align-items: flex-end;
  background: url('../img/sec-1-back-mob.jpg') no-repeat center top;
  background-size: 100%;
  background-color: #0069AB;
  @include media-breakpoint-up('sm') {
    background: #1FE0DB;
  }
  .bg-banner {
    position: absolute;
    width: 100%;
    height: auto;
    z-index: -2;
    display: none;
    @include media-breakpoint-up('sm') {
      display: block;
    }
  }
  &:after {
    @include media-breakpoint-up('sm') {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      background: linear-gradient(180deg, rgba(#0069AB, 0) 0px, #0069AB 40px, #0069AB 100%);
      height: calc(30% + 40px); 
      width: 100%;
      z-index: -1;
    }
    @include media-breakpoint-up('lg') {
      display: none;
    }
  }
  .bg-banner-1 {
    left: 0;
    bottom: 30%;
    margin-bottom: 9%;
    @include media-breakpoint-up('lg') {
     bottom: 0%;
    }
  }
  .bg-banner-2 {
    left: 0;
    bottom: 30%;
    @include media-breakpoint-up('lg') {
      bottom: 0%;
    }
  }
  .row {
    align-items: flex-end;
    .col-lg-6:first-child {
      align-self: center;
      margin-left: auto;
      margin-right: auto;
      @include media-breakpoint-down('md') {
        .svg-background-1 {
          margin-top: 20px !important;
        } 
      }
      @include media-breakpoint-down('xs') {
        .svg-background-1 {
          margin-top: -18px !important;
          margin-bottom: 43px !important;
        } 
      }
      @include media-breakpoint-up('lg') {
        margin-left: 0;
        margin-right: 0;
        padding-bottom: 10%;
      }
      
    }
    .col-lg-6:last-child {
      padding-left: 0;
      padding-right: 0;
      padding-top: 50px;
      @include media-breakpoint-up('sm') {
        padding-right: 10px;
        padding-left: 10px;
        padding-top: 0;
      }
      img {
        transform: translate(-12.375%, 0);
        max-width: 110.6%;
        @include media-breakpoint-up('sm') {
          max-width: 120%;
          transform: translate(-10%, 0);
        }
        @include media-breakpoint-up('lg') {
          transform: translate(0, 0);
          max-width: 100%;
        }
      }
    }
  }
}

.section-2 {
  overflow: hidden;
  padding-top: 35px;
  padding-bottom: 70px;
  .row {
    align-items: center;
    .col-lg-6:first-child {
      text-align: center;
      @include media-breakpoint-up('lg') {
        text-align: left;
      }
    }
    .col-lg-6:last-child {
      margin-left: auto;
      margin-right: auto;
      margin-top: 25px;
      @include media-breakpoint-up('lg') {
        margin-right: 0;
        margin-top: 0;
      }
    }
  }
}

.section-3 {
  padding-top: 73px;
  padding-bottom: 73px;
  .row {
    justify-content: center;
    margin-top: -60px;
    & > * {
      margin-top: 60px;
    }
    @include media-breakpoint-up('md') {
      margin-top: -40px;
      & > * {
        margin-top: 40px;
      }
    }
  }
}

.section-4 {
  background: url('../img/bg-sec-4.png');
  background-position: center bottom;
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 50px;
  .row {
    align-items: flex-end;
    .col-xxl-5:first-child {
      align-self: center;
      padding-top: 20px;
      @include media-breakpoint-up('lg') {
        padding-bottom: 20px;
      }
      .big-ul-list {
        max-width: 20em;
        margin-left: auto;
        margin-right: auto;
        @include media-breakpoint-up('lg') {
          max-width: none;
        }
      }
    }
  }
}

.section-5 {
  .col-lg-6:first-child {
    max-width: 555px;
    margin-left: auto;
    margin-right: auto;
    @include media-breakpoint-up('lg') {
      margin-right: 35px;
      flex: 0 0 calc(50% - 35px);
      margin-left: 0;
    }
    @include media-breakpoint-up('xl') {
      margin-right: 50px;
      flex: 0 0 calc(50% - 50px);
    }
  }
}

.section-6 {
  .col-lg-6:nth-child(2) {
    max-width: 555px;
    margin-left: auto;
    margin-right: auto;
    @include media-breakpoint-up('lg') {
      margin-right: 0;
      margin-left: 35px;
      flex: 0 0 calc(50% - 35px);
    }
    @include media-breakpoint-up('xl') {
      margin-left: 50px;
      flex: 0 0 calc(50% - 50px);
    }
  }
}

.section-5, .section-6 {
  padding-top: 55px;
  padding-bottom: 55px;
}

.section-8 {
  background: url('../img/sec-8-back.jpg') no-repeat center bottom;
  background-size: cover;
  .row {
    align-items: center;
    & > div:first-child {
      padding-top: 40px;
      padding-bottom: 40px;
      
      @include media-breakpoint-down('md') {
        max-width: 600px;
        margin-left: auto;
        margin-right: auto;
        padding-top: 60px;
        padding-bottom: 60px;
      }
      & > img {
        margin-bottom: -2%;
      }
      & > .h1 {
        max-width: (630em/36);
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        @include media-breakpoint-up('lg') {
          text-align: left;
          margin-right: -3em;
          margin-left: 0;
        }
      }
    }
    & > div:last-child {
      align-self: flex-end;
    }
  }
}

.section-9 {
  background: url('../img/sec-9-back.jpg') no-repeat center bottom;
  background-size: cover;
  padding-top: 45px;
  padding-bottom: 60px;
  .row {
    align-items: center;
    & > div:first-child {
      text-align: center;
      margin-bottom: 30px;
      @include media-breakpoint-up('lg') {
        text-align: left;
        margin-bottom: 0;
      }
    }
    & > div:last-child {
      @include media-breakpoint-down('md') {
        max-width:555px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
}