.box-image {

}
.box-image-1 {
  display: inline-flex;
  align-items: center;
  max-width: 470px;
  width: calc(100% - 30px);
  $widthHeart: (7900%/470);
  $widthTrophy: (13300%/470);
  $widthRocet: (10700%/470);
  @include media-breakpoint-up('sm') {
    margin-left: 10%;
    width: 80%;
  }
  @include media-breakpoint-up('lg') {
    margin-left: 0;
  }
  @include media-breakpoint-down('xs') {
    margin-top: 75px !important;
    margin-bottom: 70px !important;
  }
  &:before {
    content: '';
    padding-top: (38900%/470);
    width: 0;
    display: block;
  }  
  .circle-pulse {
    width: 90%;
  }
  img:first-child {
    flex: 0 0 auto;
    width: 115%;
    max-width: none;
    @include media-breakpoint-up('sm') {
      width: 100%;
    }
  }
  .heart, .trophy, .rocet {
    position: absolute;
  }
  .heart {
    width: 65px;
    bottom: 103%;
    right: 46%;
    margin-right: -32px;
    @include media-breakpoint-up('sm') {
      width: $widthHeart;
      bottom: 105%;
      right: 5%;
      margin-right: 0;
    }
  }
  .trophy {
    width: 91px;
    right: -27px;
    bottom: -50px;
    @include media-breakpoint-up('sm') {
      width: $widthTrophy;
      right: -5%;
      bottom: -20%;
    }
  }
  .rocet {
    width: 70px;
    left: -3px;
    bottom: -70px;
    @include media-breakpoint-up('sm') {
      width: $widthRocet;
      right: 100%;
      left: auto;
      bottom: auto;
      top: 25%;
    }
  }
}