.circle-pulse {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  .circle {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale(0);
    width: 100%;
    border-radius: 50%;
    // border: 2px solid #fff;
    background: $text-color;
    &:before {
      content: '';
      display: block;
      width: 0;
      padding-top: 100%;
    }
  }
  .circle:nth-child(1) {
    animation: circle-pulse 3s ease-out infinite; 
  }
  .circle:nth-child(2) {
    animation: circle-pulse 3s .75s ease-out infinite; 
  }
  .circle:nth-child(3) {
    animation: circle-pulse 3s 1.5s ease-out infinite; 
  }
}

@keyframes circle-pulse {
  0% {
      transform: translate(-50%, -50%) scale(0);
      opacity: 0.7;
  }
  100% {
      transform: translate(-50%, -50%) scale(1.7);
      opacity: 0;
  }
}