.form-slider {
  background: #fff;
  clip-path: url(#formSliderClipMOB);
  color: $text-color;
  padding-top: 24px;
  padding-bottom: 31px;
  @include media-breakpoint-up('sm') {
    clip-path: url(#formSliderClip);
  }
  @include media-breakpoint-up('md') {
    margin-left: -(4800%/555);
    margin-right: -(4800%/555);
    padding-top: (4100%/555);
    padding-bottom: (3600%/555);
  }
  &__wrapper {
    overflow: hidden;  
    padding-left: 29px;
    padding-right: 29px;
    @include media-breakpoint-up('md') {
      padding-left: (4800%/555);
      padding-right: (4800%/555);
    }
  }
  &__outer {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    height: 0;
    transition: height 0.3s, transform 0.3s;
  }
  &__item {
    width: 100%;
    flex: 0 0 100%;
    margin-right: (7000%/555);
    // position: absolute;
    & > br {
      @include media-breakpoint-down('md') {
        display: none;
      }
    }
    & > .btn {
      @include media-breakpoint-down('md') {
        margin-top: 10px;
      }
    }
  }
}