.btn {
  // box-shadow: -1px 3px 3px rgba(0, 105, 192, 0.3);
  font-weight: bold;
  font-size: (18rem/16);
  line-height: 1;
  text-align: center;
  color: $text-color-invert;
  text-shadow: 0px 2px 2px rgba(0, 46, 85, 0.25);
  display: inline-flex;  
  align-items: center;
  justify-content: center;
  min-width: (182em/18);
  position: relative;
  z-index: 0;
  padding: 5px 30px 11px 23px;
  svg.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    path:first-child {
      fill: url(#gradForBtn);
      opacity: 1;
      transition: 0.3s;
    }
    path:last-child {
      fill: url(#gradForBtnHover);
      opacity: 0;
      transition: 0.3s;
    }
  }
  &:before {
    content: '';
    display: block;
    width: 0;
    padding-top: (4500%/172);
  }

  &:hover {
    color: $text-color-invert;
    svg.background {
      path:last-child {
        opacity: 1;
      }
    }
  }
}
.btn--small {
  min-width: (128em/18);
  padding: 0 5px 5px;
}
.btn--min-height {
  min-height: (50em/18);
  &:before {
    display: none;
  }
}
a.btn {
  &:hover {
    text-decoration: none;
  }
}