.cart-icon-circle {
  &__icon-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 26px;
    width: 100%;
    justify-content: center;
    &:before, &:after {
      content: '';
      height: 1px;
      background: rgba(#0098B8, 0.2);
      flex: 1 1 0px;
      display: none;
      @include media-breakpoint-up('lg') {
        display: block;
      }
    } 
    &:before {
      margin-left: -15px;
    } 
    &:after {
      margin-right: -15px;
    } 
  }
  &__icon {
    border: 1px solid rgba(#0098B8, 0.2);
    border-radius: 50%;
    max-width: 226px;
    padding: 17px;
    position: relative;
    width: 100%;
    flex: 0 0 auto;
  }
  &__circle {
    background: url('../img/cart-icon-green-notopt.svg') no-repeat center center;
    background-size: contain;
    border-radius: 50%;
    width: 100%;
    padding: 10px;
    display: flex;
    align-items: center;
    position: relative;
    &:before {
      content: '';
      width: 0;
      display: block;
      padding-top: 100%;
    }

    img {
      position: absolute;
      &.centered {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
  &__content {
    text-align: center;
    @include media-breakpoint-up('lg') {
      text-align: left;
    }
    .h2 {
      text-align: center;
      margin-bottom: 1em;
    }
  }
}

.cart-icon-circle:first-child {
  .cart-icon-circle__icon-wrapper:before {
    opacity: 0;
  } 
}
.cart-icon-circle:last-child {
  .cart-icon-circle__icon-wrapper:after {
    opacity: 0;
  }
}

.cart-icon-circle--blue {
  .cart-icon-circle__circle {
    background-image: url('../img/cart-icon-blue-notopt.svg');
  }
}
.cart-icon-circle--yellow {
  .cart-icon-circle__circle {
    background-image: url('../img/cart-icon-yellow-notopt.svg');
  }
}