.social-links {
  &__wrapper {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin: -10px;
  }
  &__item {
    overflow: hidden;
    transition: 0.2s;
    opacity: 1;
    margin: 10px;
    width: 46px;
    height: 46px;
    position: relative;
    img {
      max-width: none;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -51%);
      width: (6000%/46);
    }
    &:before {
      content: '';
      position: absolute;
    } 
    &:hover {
      opacity: 0.6;
      // background: radial-gradient(closest-corner, #DE00DE 40%, transparent);
    }
  }
}

@include media-breakpoint-down('xs') {
  .social-links--sm {
    .social-links__wrapper {
      margin: -5px;
    }
    .social-links__item {
      margin: 5px;
      width: 32px;
      height: 32px;
    }
  }
}
